	methods: {
		getoorder(e){
			console.log(e);
			var id = e.currentTarget.id;
			
			uni.navigateTo({
				url: '../order/order?id=' + id
			});
		},
		toclassleft(){
			var state = this.cleft;
			if(this.cright == true){
				this.cright = false;
				this.num --;
			};
			var len = this.num;
			this.isActive = (len % 2 == 0) ? true : false;
			if(state == true){
				this.cleft = false
			}else if (state == false){
				this.cleft = true
			};
			this.num ++;
			if(this.cleft == false && this.cright == false){
				this.isActive = false;
			};
			if(this.cleft == true || this.cright == true){
				this.isActive = true;
			}
		},
		toclassright(){
			if(this.cleft == true){
				this.cleft = false;
				this.num --;
			};
			var len = this.num;
			this.isActive = (len % 2 == 0) ? true : false;
			this.cright = (this.cright == true) ? false : true;
			this.num ++;
			if(this.cleft == false && this.cright == false){
				this.isActive = false;
			};
			if(this.cleft == true || this.cright == true){
				this.isActive = true;
			}
		},
		cleftback(){
			this.cleft = false;
			this.isActive = false;
			this.cright = false;
		},
		tothere(e){
			var num = e.currentTarget.id;
			var len = this.listclass1.length;
			var a = this.listclass1[num].active;
			if(a == true){
				return
			}else if(a == false){
				for(var i = 0;i < len;i++){
					this.listclass1[i].active = false;
				};
				this.listclass1[num].active = true;
			}
		},
		totherea(e){
			var num = e.currentTarget.id;
			var len = this.listclass2.length;
			var a = this.listclass2[num].active;
			if(a == true){
				return
			}else if(a == false){
				for(var i = 0;i < len;i++){
					this.listclass2[i].active = false;
				};
				this.listclass2[num].active = true;
			}
		},
		tothereb(e){
			var num = e.currentTarget.id;
			var len = this.listclass3.length;
			var a = this.listclass3[num].active;
			if(a == true){
				return
			}else if(a == false){
				for(var i = 0;i < len;i++){
					this.listclass3[i].active = false;
				};
				this.listclass3[num].active = true;
			}
		}
	},
	onReachBottom() {
		console.log(this.pages);
		var page = this.pages;
		if(page <= 9){
			this.listdata = this.listdata.concat(this.newlist);
			this.pages++;
		}else if(page > 9){
			return
		}
		console.log(this.pages);
	}
